import {
  Flex,
  HStack,
  Icon,
  SimpleGrid,
  Stack,
  Text,
  Spinner,
  Center,
  Button,
  useToast,
} from "@chakra-ui/react";
// import NavBar from "../../../components/NavBar";
import { FiChevronLeft } from "react-icons/fi";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import DashboardProductItem from "../../../components/DashboardProductItem";
import { nanoid } from "nanoid";
import { Suspense, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { AuthReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import SkeletonLoader from "../../../components/SkeletonLoader";
// import DashboardNavBar from "../../../components/DashboardNavBar";
import NewDashboardNavBar from "../../../components/NewDashboardNavBar";
import { FaAngleDown } from "react-icons/fa";
import Container from "../../../components/Container";
import axios from "axios";
import { BASE_URL } from "../../../config";
import MeProtocolToast from "../../../hooks/MeProtocolToast";
import { PopularDeaslsSvg } from "../../../assets/iconsSvg/iconsSvg";
import { GLOBAL_PREFIX } from "../../../constants";
import { Sort } from "../../../redux/rtkQuery/api";
import moment from "moment";

const SeeAllOffersInCategory = (): JSX.Element => {
  let navigate = useNavigate();
  const { root, loadMore } = useStyles();
  let toast = useToast();

  const { id } = useParams();
  const { state }: { state: any } = useLocation();
  const [allOffers, setAllOffers] = useState<Array<any>>([]);
  const [loadedOffers, setLoadedOffers] = useState<Array<any>>([]);
  const [loadingMore, setLoadingMore] = useState<boolean>(false);
  const [page, setPage] = useState<number>(1);

  const [reachedEnd, setReachedEnd] = useState<boolean>(false);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [offersFetching, setOffersFetching] = useState<boolean>(false);
  const [sortCriteria, setSortCriteria] = useState<Sort>(Sort.POPULAR)

  const { token, session_id }: AuthReducerProps = useSelector(
    (state: RootState) => state?.authReducer
  );

  // console?.log(token);

  // const { data, isLoading }: any = useGetAllOffersForFiltersQuery(
  //   { filter: `${state ? state : "popular"}`, page, limit: 8 },
  //   { refetchOnMountOrArgChange: true }
  // );

  // const { data: recentlyViewed, isLoading: recentlyViewedFetching }: any =
  //   useGetRecentlyViewedOffersQuery(
  //     { session_id },
  //     {
  //       refetchOnMountOrArgChange: 100,
  //     }
  //   );

  // console?.log(state);

  useEffect(() => {
    {
      state ? GET_ALL_OFFERS() : GET_RECENTLY_VIEWED_OFFERS();
    }
  }, [page]);

  // function LoadMore() {
  //   setLoadingMore(true);

  //   try {
  //     setAllOffers([...allOffers, ...data.data.offers]);
  //   } catch (e) {
  //     console?.log(e, "LoadMoreErr");
  //   } finally {
  //     setLoadingMore(false);
  //   }
  // }

  // --------------------------------------------------------------NEW FETCH IMPLEMENTATION------------------------------------
  async function GET_ALL_OFFERS() {
    setOffersFetching(true);
    setReachedEnd(false);
    try {
      const { data }: any = await axios.get(
        `${BASE_URL}/store/offer?page=${page}&limit=8&orderBy=MOST_VIEWED&sort=${
          state ? state : sortCriteria
        }`,
        { headers: { Authorization: `Bearer ${token}` } }
      );
      // console?.log("once");

      if (data?.data?.offers.length < 1) {
        setReachedEnd(true);
        MeProtocolToast(toast, "No more offer available", 3000, "top");
      }
      setLoadedOffers([...loadedOffers, ...data?.data?.offers]);
      setTotalPages(data?.data?.pagination?.totalPage);
    } catch (e: any | unknown) {
      console?.log(e, "GET_ALL_OFFERS_ERR");
    } finally {
      setOffersFetching(false);
    }
  }

  async function GET_RECENTLY_VIEWED_OFFERS() {
    setOffersFetching(true);
    setReachedEnd(false);
    try {
      const { data }: any = await axios.get(
        `${BASE_URL}/views/recently-viewed/${session_id}?page=${page}&limit=8`
      );

      if (data?.data?.length < 1) {
        setReachedEnd(true);
        MeProtocolToast(toast, "No more offer available", 3000, "top");
      }
      setLoadedOffers([...loadedOffers, ...data?.data]);
      // setTotalPages(data?.data?.pagination?.totalPage);
    } catch (e: any | unknown) {
      console?.log(e, "GET_ALL_OFFERS_ERR");
    } finally {
      setOffersFetching(false);
    }
  }

  return (
    <Flex flexDir={"column"}>
      <NewDashboardNavBar />
      <Container>
        <Stack {...root}>
          <HStack as={"button"} onClick={() => navigate(-1)}>
            <Icon as={FiChevronLeft} w={"2vw"} h={"2vw"} />
            <Text>Back</Text>
          </HStack>
          <Container>
            <HStack mt="20px">
              <PopularDeaslsSvg />
              <Text fontSize={["18px", "18px"]} textTransform="uppercase">
                {state?.replaceAll("_", " ")}
              </Text>
            </HStack>
          </Container>

          {state && (
            <>
              {/* {offersFetching ? (
                <SkeletonLoader />
              ) : ( */}
              <SimpleGrid
                columns={[2, 2, 3, 4, 4]}
                spacingX={["35px", "35px", "35px", "15px", "35px"]}
                w={"100%"}
              >
                {loadedOffers?.map((e, i: number) => (
                  <DashboardProductItem
                    key={nanoid()}
                    isLoaded={true}
                    productTitle={e?.name}
                    points={`${e?.tokens}${e?.reward?.rewardSymbol}`}
                    productSubtitle={e?.product?.category?.name}
                    price={e?.originalPrice?.toLocaleString()}
                    isNew={moment(moment()).diff(e?.createdAt, "days") < 1}
                    // trending={e?.trending}
                    rest={{ ...e }}
                    i={i}
                    bgImage={e?.offerImages}
                    onClick={() =>
                      // navigate(`${GLOBAL_PREFIX}/productDetails/${e?.slug}`, {
                      //   state: { ...e },
                      // })
                      navigate(`${GLOBAL_PREFIX}/productDetails/${e?.offerCode}`)
                    }
                  />
                ))}
              </SimpleGrid>
              {/* )} */}
            </>
          )}

          {!state && (
            <>
              {offersFetching ? (
                <SkeletonLoader />
              ) : (
                <SimpleGrid columns={[2, 2, 3, 4, 4]} w={"100%"} spacingX={"3.5%"} mt="40px">
                  <Suspense fallback={<Spinner size="sm" />}>
                    {loadedOffers?.map((e, i: number) => (
                      <DashboardProductItem
                        key={nanoid()}
                        isLoaded={true}
                        productTitle={e?.name}
                        points={`${e?.tokens}${e?.reward?.rewardSymbol}`}
                        productSubtitle={e?.product?.category?.name}
                        price={e?.originalPrice?.toLocaleString()}
                        isNew={moment(moment()).diff(e?.createdAt, "days") < 1}
                        // trending={e?.trending}
                        rest={{ ...e }}
                        i={i}
                        bgImage={e?.offerImages}
                        onClick={() =>
                          // navigate(`${GLOBAL_PREFIX}/productDetails/${e?.slug}`, {
                          //   state: { ...e },
                          // })
                          navigate(`${GLOBAL_PREFIX}/productDetails/${e?.offerCode}`)
                        }
                      />
                    ))}
                  </Suspense>
                </SimpleGrid>
              )}
            </>
          )}

          {/* LOAD MORE BUTTON */}
          {state && (
            <>
              {!reachedEnd && (
                <Center
                  as={Button}
                  {...loadMore}
                  onClick={() => {
                    setPage(page + 1);
                  }}
                >
                  {offersFetching ? (
                    <Spinner size={"sm"} />
                  ) : (
                    <>
                      Load more <Icon as={FaAngleDown} ml={"5%"} />
                    </>
                  )}
                </Center>
              )}
            </>
          )}
        </Stack>
      </Container>
    </Flex>
  );
};

export default SeeAllOffersInCategory;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      // px: [5, 5, 10, 20, 40],
      mt: "108px",
      pt: "3%",
      overflow: "hidden",
      pb: "2%",
    },
    loadMore: {
      w: "132px",
      h: "32px",
      alignSelf: "center",
      py: "10px",
      borderRadius: "100px",
    },
  };
};
